import {
  watch, Ref, getCurrentInstance, onUnmounted, onMounted,
} from '@vue/composition-api';
import Vue from 'vue';
import 'intersection-observer';

export function callbackWhenLastElementShown(
  list: Ref<any[]|null>, callback: ()=> any, targetSelector: string
):void;

export function callbackWhenLastElementShown(
  list: Ref<any[]|null>, callback: ()=> any, container: Ref<Element>
):void;

export function callbackWhenLastElementShown(
  list: Ref<any[]|null>, callback: ()=> any, targetSelectorOrSelector: string | Ref<Element>,
) {
  let io:IntersectionObserver;
  let watcher: any;
  const currentVm = getCurrentInstance();
  onMounted(() => {
    watcher = watch(() => list?.value, async (nL) => {
      if (nL && nL.length && nL.length > 0) {
        await currentVm?.$nextTick();
        let target: Element;
        if (typeof targetSelectorOrSelector === 'string') {
          const targets = currentVm!.$el.querySelectorAll(targetSelectorOrSelector);
          target = targets[targets.length - 1];
        } else {
          target = targetSelectorOrSelector.value;
        }
        io = new IntersectionObserver((entries, obs) => {
          entries.forEach(entry => {
            if (entry.intersectionRatio > 0.9 && entry.target === target) {
              obs.unobserve(entry.target);
              callback();
            }
          });
        }, {
          root: null,
          rootMargin: '0px 0px 0px 0px',
          threshold: 0.9,
        });
        io.observe(target);
      }
    }, {
      immediate: true,
    });
  });
  const terminate = () => {
    if (watcher) {
      watcher();
    }
    if (io) {
      io.disconnect();
    }
  };
  onUnmounted(terminate);
  return terminate;
}

export function callbackWhenLastElementShownNotCompositionVersion(
  listName: string, callback: ()=> any, targetSelectorOrSelector: string | Element,
) {
  let io:IntersectionObserver;
  // @ts-ignore
  const currentVm = (this as InstanceType<typeof Vue>);
  currentVm.$watch(listName, async (nL) => {
    if (nL && nL.length > 0) {
      await currentVm.$nextTick();

      let target;

      if (typeof targetSelectorOrSelector === 'string') {
        const targets = currentVm!.$el.querySelectorAll(targetSelectorOrSelector);
        target = targets[targets.length - 1];
        // console.log(target);
      } else {
        target = targetSelectorOrSelector.children[targetSelectorOrSelector.childElementCount - 1];
      }
      io = new IntersectionObserver((entries, obs) => {
        entries.forEach(entry => {
          // console.log(entry.intersectionRatio);
          if (entry.intersectionRatio === 1) {
            obs.unobserve(entry.target);
            callback();
          }
        });
      }, {
        root: null,
        rootMargin: '0px 0px 0px 0px',
        threshold: 1,
      });
      io.observe(target);
    }
  });
  const { beforeDestroy } = currentVm.$options;
  currentVm.$options.beforeDestroy = function newBeforeDestroy() {
    // console.log('beforedestroy');
    if (io) {
      io.disconnect();
    }
    if (beforeDestroy) {
      beforeDestroy.call(currentVm);
    }
  };
}

export default callbackWhenLastElementShown;
