















































import Vue from 'vue';
import {
  defineComponent, ref, Ref, reactive, computed, watch,
} from '@vue/composition-api';
import axios from '@/api/axios';
import useLoader from '_/composables/use-loader';
import useClosest from '_/composables/use-closest';
import { callbackWhenLastElementShown } from '@/utils/composables/use-callback-when-last-element-shown';
// import { getCurrentCompany } from '@/loaders/company';
import CompanyInfo from '@/views/supplier-backend/company-info/index.vue';
import SupplierPage from '@/views/supplier-page/index.vue';
import DisplayModeSwitcher from '@/views/material-mall/components/DisplayModeSwitcher.vue';
import isQt from '@/utils/is-qt';
import Preview from '../index.vue';

const HomePageDefault = defineComponent({
  name: 'HomePageDefault',
  components: {
    CompanyInfo,
    DisplayModeSwitcher,
  },
  props: {
    companyId: {
      type: String,
      default: '',
    },
    materialItem: {
      type: [Function, Object],
    },
  },
  setup(props, { root }) {
    const preview = useClosest<typeof Preview>('Preview')?.value;
    const MaterialItem = props.materialItem ? props.materialItem : preview?.$props.materialItem;
    const supplierPage = useClosest<typeof SupplierPage>('SupplierPage')?.value;
    supplierPage && (supplierPage.backgroundClass = '');
    const displayMode = ref('drape');

    const activeName = ref('new');
    const params = reactive({
      companyId: props.companyId,
      keyword: '',
      orderBy: 'click',
      asc: false,
      supplierArticleName: '',
      size: 50,
    });
    function handleClick() {
      // 默认全部面料
      params.keyword = '';
      params.orderBy = 'click';
      params.asc = false;
      params.size = 50;
      if (activeName.value === 'new') {
        params.keyword = '';
        params.orderBy = 'gmtStoreTime';
        params.asc = false;
        params.size = 50;
      } else if (activeName.value === 'mostViewed') {
        params.keyword = '';
        params.orderBy = 'click';
        params.asc = false;
        params.size = 50;
      } else if (activeName.value === 'staffPicks') {
        params.keyword = '推荐|Staff Picks';
      } else if (activeName.value === 'coreCollection') {
        params.keyword = '主营|Core Collection';
      } else if (activeName.value === 'inSeason') {
        params.keyword = '当季|In Season';
      }
    }
    watch(() => params.keyword, (val) => {
      if (activeName.value === 'supplierBrief') {
        activeName.value = 'all';
      }
    });
    // const company = computed(() => {
    //   return getCurrentCompany();
    // });
    const company = useLoader(async ({ companyId }) => {
      const res = (await axios({
        method: 'get',
        url: '/authcenter/company/get/info/id',
        params: {
          id: companyId,
        },
      })).data;
      return res;
    }, params, {
      initialValue: null,
    });
    let totalPage: number;
    async function fetchGoodsList({
      companyId = params.companyId,
      keyword = params.keyword,
      orderBy = params.orderBy,
      asc = params.asc,
      supplierArticleName = params.supplierArticleName,
      page = 1,
      size = params.size,
    }) {
      const useFilters = !!supplierArticleName;
      const res = (await axios({
        url: '/mall/supplier/materials/published',
        method: 'post',
        data: {
          page,
          size,
          orders: [{
            name: orderBy,
            value: asc,
          }],
          searchs: keyword,
          filters: useFilters ? [
            {
              name: 'companyId',
              value: companyId,
            },
            {
              name: 'supplierArticleName',
              value: supplierArticleName,
            },
          ] : [{ name: 'companyId', value: companyId }],
        },
      })).data;
      return res;
    }
    let currentPage: number;
    const goods = useLoader(async (args) => {
      const res = await fetchGoodsList(args);
      currentPage = 1;
      totalPage = res.pages;
      return res.records;
    }, params);


    async function loadMore(targetPage = currentPage + 1) {
      if (Array.isArray(goods.value) && targetPage <= totalPage) {
        goods.value.push(...(await fetchGoodsList({
          ...params,
          page: targetPage,
        })).records);
        currentPage++;
      }
    }

    if (!params.size) {
      // loadMore();
      callbackWhenLastElementShown(goods, loadMore, '.material-good-item');
    }

    // let iObserver:IntersectionObserver;

    // watch(() => goods?.value, async (nL) => {
    //   if (nL) {
    //     if (iObserver) iObserver.disconnect();
    //     await root.$nextTick();
    //     const targets = document.querySelectorAll('.material-good-item');
    //     const target = targets[targets.length - 1];
    //     console.log(target);
    //     iObserver = new IntersectionObserver(() => loadMore(), {
    //       threshold: 1,
    //     });
    //     iObserver.observe(target);
    //   }
    // });
    // async function changeOrder(order: {order: string, asc: boolean}) {
    //   const { query } = root.$route;
    //   root.$router.replace({
    //     query: {
    //       ...query,
    //       orderBy: order.order,
    //       asc: order.asc.toString(),
    //     },
    //   });
    // }

    async function previewList(index: number) {
      const list = goods?.value.map((e: any) => e?.imgJson?.thumbnail) as String[];
      await root.$ImageViewer.show(list, {
        index,
        targetElems: '.material-img',
      });
    }

    return {
      goods,

      // changeOrder,
      loadMore,
      previewList,

      activeName,
      handleClick,
      company,
      MaterialItem,
      params,
      displayMode,
      isQt,
    };
  },
});
export default HomePageDefault;
