
import { defineComponent, ref } from '@vue/composition-api';
import axios from '@/api/axios';
import Preview from '@/views/supplier-backend/preview/index.vue';
import HomePageDefault from '@/views/supplier-backend/preview/homepage/HomePageDefault.vue';
import MaterialItemDesignerVersion from '../../supplier-backend/preview/components/MaterialItemDesignerVersion.vue';

const getMallTemplateConfig = async (companyId) => {
  const result = (await axios.get('/msgcenter/config', {
    params: {
      id: companyId,
      type: 6,
    },
  })).data;
  // console.log('get config=======', result);
  return result || {};
};

const SupplierPreview = defineComponent({
  name: 'SupplierPreview',
  props: {
    companyId: String,
    keyword: String,
  },
  setup(props, { emit }) {
    // 获取商家主页配置，为默认主页，则直接展示默认的商家主页，否则展示自定义的主页
    const config = ref({});
    (async function init() {
      config.value = await getMallTemplateConfig(props.companyId);
    }());

    return {
      config,
    };
  },
  render(h) {
    const self = this as InstanceType<typeof SupplierPreview>;
    // return <CompanyBrief companyId={self.companyId!}/>;
    // return <Preview materialItem={MaterialItemDesignerVersion}
    //   companyId={self.companyId!} keyword={self.keyword} />;
    if (self.config.mode === 'customer') {
      return <Preview materialItem={MaterialItemDesignerVersion}
        companyId={self.companyId!} keyword={self.keyword} />;
    }
    return <HomePageDefault materialItem={MaterialItemDesignerVersion} companyId={self.companyId} />;
  },
});
export default SupplierPreview;
