
import { defineComponent, watch, ref } from '@vue/composition-api';
import i18n from 'i18next';
import useLoader from '_/composables/use-loader';
import axios from '../../../api/axios';

const preview = defineComponent({
  name: 'Preview',
  props: {
    companyId: {
      type: String,
      required: true,
    },
    materialItem: {
      type: [Function, Object],
    },
  },
  render() {
    const self = this as InstanceType<typeof preview>;
    if (self?.companyInfo) {
      const { companyInfo } = self;
      return <main class='flex-column' style='flex: 1; min-width: 1250px;'>
        <section style='flex: 1' class='flex-column'>
          <div class='flex-justify-center' style='flex: 1'>
            <router-view companyId={self.companyId}></router-view>
          </div>
        </section>
      </main>;
    }
    return <div vLoading={true} style='width: 100%; height: 300%;'></div>;
  },
  setup(props, { root }) {
    watch(() => root!.$route, (newRoute, oldRoute) => {
      if (newRoute.path.endsWith('preview')) {
        root.$router.replace({
          query: {
            ...root.$route.query,
          },
          path: 'preview/homepage',
        });
      }
    }, {
      immediate: true,
    });

    const companyInfo = useLoader(async ({ companyId }) => {
      const res = (await axios({
        method: 'get',
        url: '/authcenter/company/get/info/id',
        params: {
          id: companyId,
        },
      })).data;
      return res;
    }, props, {
      initialValue: null,
    });

    return {
      companyInfo,
    };
  },
});
export default preview;
